import { LitElement, html } from "lit";
import { customElement, property, query, state } from "lit/decorators.js";
import { unsafeHTML } from "lit-html/directives/unsafe-html.js";

import {
  AvailableJob,
  JobIdStatesMap,
  JobParameters,
  ProcessingState,
  SomeJSONSchema,
} from "../../lib/types";

import {
  isActiveProcessingState,
  isoStringToDateString,
} from "../../lib/helpers";

import "../../lib/webservices/src/aitLoadingSpinner/index";
import styles from "./arch-job-card-styles";

@customElement("arch-job-card")
export class ArchJobCard extends LitElement {
  @property() collectionId!: string;
  @property() job!: AvailableJob;
  @property() jobIdStatesMap!: JobIdStatesMap;
  @property() collectionDetailPageUrlTemplate!: string;

  @query("button.job-button") button!: HTMLButtonElement;

  @state() jobParameters: object | JobParameters = {};

  static styles = styles;

  private startTimeStatePairsToButtonProps(
    startTimeStatePairs: JobIdStatesMap[keyof JobIdStatesMap] | undefined | null
  ): [string, string, boolean] {
    /*
      Return the [buttonHTML, className, disabled] tuple for the specified
      startTimeStatePairs
    */
    switch (startTimeStatePairs) {
      case undefined:
        // Job states are loading - likely in response to a collection selection change.
        return [this.collectionId ? "Loading..." : "n/a", "", true];
        break;
      case null:
        // No previous runs exist for this job.
        return [`Generate Dataset`, "primary", false];
        break;
    }

    const latestState = startTimeStatePairs[0][1];
    switch (latestState) {
      case ProcessingState.FINISHED:
        // The most recent job run has finished.
        return [`Generate New Dataset`, "primary", false];
        break;
      case ProcessingState.SUBMITTED:
        // A new job was submitted.
        return [
          `
            <div style="display: flex;">
              <span style="margin: auto 0.5rem auto 0;">Starting</span>
              <ait-loading-spinner style="margin: auto 0; --ait-loading-spinner-color: #2991CC"></ait-loading-spinner>
            </div>
          `,
          "",
          true,
        ];
        break;
      default:
        // The job is probably running.
        return [latestState, "", isActiveProcessingState(latestState)];
        break;
    }
  }

  static DefaultParametersSchema: SomeJSONSchema = {
    type: "object",
    required: [],
    properties: {},
  };

  private extendParamsSchemaWithDefaultOptions(
    schema: AvailableJob["parameters_schema"]
  ): AvailableJob["parameters_schema"] {
    schema = schema ?? ArchJobCard.DefaultParametersSchema;
    return Object.assign(schema, {
      properties: Object.assign(
        schema.properties as Record<string, SomeJSONSchema>,
        {
          sample: {
            type: "boolean",
            title: "Sample",
            default: true,
            description:
              "Generate a sample dataset from a small subset of records",
          },
        }
      ),
    });
  }

  get historicalDatasetsUrl() {
    const { collectionDetailPageUrlTemplate, collectionId, job } = this;
    const path = collectionDetailPageUrlTemplate.replace("0", collectionId);
    return `${path}?column-name=${encodeURIComponent(job.name)}`;
  }

  renderHistory() {
    const { jobIdStatesMap, job } = this;
    const startTimeStatePairs = jobIdStatesMap && jobIdStatesMap[job.id];
    if (startTimeStatePairs === undefined || startTimeStatePairs.length === 0) {
      return html``;
    }
    const finishedStates = startTimeStatePairs.filter(
      ([, state]) => state === ProcessingState.FINISHED
    );
    if (finishedStates.length === 0) {
      return html``;
    }
    const aText =
      finishedStates.length > 1
        ? "Click here to view these previous datasets"
        : "Click here to view this previous dataset";
    return html`
      <h4>History</h4>
      <p>
        You've generated this dataset
        <strong>${finishedStates.length}</strong> times before for this
        collection, most recently on
        <strong>${isoStringToDateString(finishedStates[0][0])}</strong>.
        <br />
        <a href="${this.historicalDatasetsUrl}" target="_blank">${aText}</a>
      </p>
    `;
  }

  renderConfigureJob() {
    const { job } = this;
    return html`
      <h4>Configure Job</h4>
      <arch-job-parameters-form
        .schema=${this.extendParamsSchemaWithDefaultOptions(
          job.parameters_schema
        )}
        .data=${this.jobParameters as JobParameters}
      ></arch-job-parameters-form>
    `;
  }

  emitGenerateDataset(e: Event) {
    e.stopPropagation();
    this.dispatchEvent(
      new CustomEvent("generate-dataset", {
        detail: { archJobCard: this },
        bubbles: true,
        composed: true,
      })
    );
  }

  render() {
    // Get the current job states.
    const { collectionId, job, jobIdStatesMap } = this;
    const { id: jobId } = job;
    // Use undefined to indicate that a job state is loading, and null to
    // indicate that no such job run exists.
    const startTimeStatePairs = !this.jobIdStatesMap
      ? undefined
      : jobIdStatesMap[jobId] ?? null;

    const [buttonHTML, buttonClass, buttonDisabled] =
      this.startTimeStatePairsToButtonProps(startTimeStatePairs);

    return html` <div>
      <h3>${job.name}</h3>
      <p>${job.description}</p>
      ${collectionId === null
        ? html`<p class="alert alert-info">
            Select a Source Collection above to display the options for
            generating a Dataset of this type.
          </p>`
        : html`
            ${this.renderHistory()} ${this.renderConfigureJob()}
            <button
              class="job-button ${buttonClass}"
              ?disabled=${buttonDisabled}
              @click=${this.emitGenerateDataset}
            >
              ${unsafeHTML(buttonHTML)}
            </button>
          `}
    </div>`;
  }
}

// Injects the tag into the global name space
declare global {
  interface HTMLElementTagNameMap {
    "arch-job-card": ArchJobCard;
  }
}
