import { html, css, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";

@customElement("ait-loading-spinner")
export class AitLoadingSpinner extends LitElement {
  @property({ type: Number }) widthPx = 24;
  @property({ type: Number }) heightPx = 24;

  render() {
    const { widthPx, heightPx } = this;
    return html`
      <style>
        .loading-spinner {
          width: ${widthPx}px;
          height: ${heightPx}px;
        }

        .loading-spinner div {
          transform-origin: ${widthPx / 2}px ${heightPx / 2}px;
        }

        .loading-spinner div:after {
          top: ${heightPx * 0.0375}px;
          left: ${widthPx * 0.4625}px;
          width: ${widthPx * 0.075}px;
          height: ${heightPx * 0.225}px;
        }
      </style>

      <div class="loading-spinner">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    `;
  }

  static styles = css`
    .loading-spinner {
      display: inline-block;
      position: relative;
    }
    .loading-spinner div {
      animation: lds-spinner 1.2s linear infinite;
    }
    .loading-spinner div:after {
      content: " ";
      display: block;
      position: absolute;
      border-radius: 20%;
      background: var(--ait-loading-spinner-color, #fff);
    }
    .loading-spinner div:nth-child(1) {
      transform: rotate(0deg);
      animation-delay: -1.1s;
    }
    .loading-spinner div:nth-child(2) {
      transform: rotate(30deg);
      animation-delay: -1s;
    }
    .loading-spinner div:nth-child(3) {
      transform: rotate(60deg);
      animation-delay: -0.9s;
    }
    .loading-spinner div:nth-child(4) {
      transform: rotate(90deg);
      animation-delay: -0.8s;
    }
    .loading-spinner div:nth-child(5) {
      transform: rotate(120deg);
      animation-delay: -0.7s;
    }
    .loading-spinner div:nth-child(6) {
      transform: rotate(150deg);
      animation-delay: -0.6s;
    }
    .loading-spinner div:nth-child(7) {
      transform: rotate(180deg);
      animation-delay: -0.5s;
    }
    .loading-spinner div:nth-child(8) {
      transform: rotate(210deg);
      animation-delay: -0.4s;
    }
    .loading-spinner div:nth-child(9) {
      transform: rotate(240deg);
      animation-delay: -0.3s;
    }
    .loading-spinner div:nth-child(10) {
      transform: rotate(270deg);
      animation-delay: -0.2s;
    }
    .loading-spinner div:nth-child(11) {
      transform: rotate(300deg);
      animation-delay: -0.1s;
    }
    .loading-spinner div:nth-child(12) {
      transform: rotate(330deg);
      animation-delay: 0s;
    }
    @keyframes lds-spinner {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  `;
}
